@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 68.5%;
}
body{
  font-family: 'PT Sans', sans-serif;
  background: #C89FA3;
}
a{
  text-decoration: none;
}
ul{
  list-style: none;
}

.navBar{
  font-size: 2.2rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 2rem 0;
  background: #DBB3B1;
}
.navBar ul{
  display: flex;
}
.navBar ul li{
  margin-left: 3rem;
}
#bar{
  display: none;
}
.sideNav{
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  transition: 0.5s;
  left: 0;
  background-color: rgb(247, 236, 236);
  overflow-x: hidden;
  font-size: 2.5rem;
}
.sideNav ul{
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}
.sideNav ul li{
  margin: 1.6rem 0;
}
.content{
  display: grid;
  grid-template-columns: 50% 50%;
  align-items: center;
  height: 80vh;
}
.left{
  text-align: center;
}
.left p:nth-child(1){
  font-size: 4rem;
}
.left p:nth-child(2){
  font-size: 2.6rem;
}
.right{
  display: flex;
  justify-content: center;
}
.content .right img{
  height: 25rem;
  width: 25rem;
}

.footer{
  position: fixed;
  bottom: 0;
  width: 100vw;
  height: auto;
  padding: 2rem 0;
  background:rgb(192, 50, 50);
  z-index: 5;
}
.footer ul{
  display: flex;
  justify-content: center;
}
.footer ul li{
  margin: 0 2rem;
  font-size: 2.5rem;
}
.footer ul li a{
    color: #fff;
}
.food-search{
  height: auto;
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}
.food-search input{
  padding: 1.8rem;
  font-size: 1.6rem;
}
.food-search button{
  border: 1px solid black;
  outline: none;
  padding: 2.15rem;
}
.foods-here{
  height: auto;
  display: grid;
  justify-content: center;
  height: auto; 
  align-items: center;
  grid-template-columns: 30% 30%;
  grid-gap: 5rem;
  text-align: center;
  margin-bottom: 10rem;
}
.food-recipe{
  display: flex;
  flex-direction: column;
  background: #DBB3B1;
}
.food-recipe p:nth-child(1){
  font-size: 2rem;
  padding: 2rem 0;
}
.food-recipe p a{
  font-size: 1.3rem;
}
.food-recipe button{
  font-size: 2rem;
}
.food-ingredients{
  padding: 0.5rem 0;
  font-size: 1.8rem;
}


/*animation*/
@keyframes slide{
  0%{
    transform: translateX(-2rem);
  }
  100%{
    transform: translateX(2rem);
  }
}
/*Media*/

@media only screen and (max-width: 720px){
  #bar{
    display: block;
  }
  .navBar ul li a{
    display: none;
  }
  .content{
    grid-template-columns: 100%;
  }
  .content .right{
    display: none;
  }
}